import { useState } from 'react';
import sprite from 'assets/images/sprite.svg';
import css from './AddBtnColumn.module.css';
import Modal from 'components/Modal/Modal';
import AddColumnForm from '../../Forms/BoardForms/AddColumnForm/AddColumnForm';

const AddBtnColumn = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleModal = () => {
    setIsOpen(isOpen => !isOpen);
  };

  const theme = localStorage.getItem('selectedTheme');
  console.log(theme);
  let iconId;
  switch (theme) {
    case 'violet':
      iconId = '#plus-violet-background';
      break;
    case 'light':
      iconId = '#plus-black-background';
      break;
    default:
      iconId = '#plus-white-background';
  }

  return (
    <>
      <button className={css.btnAddColumn} type="button" onClick={toggleModal}>
        <svg aria-label="plus" width="28" height="28">
          <use href={sprite + iconId}></use>
        </svg>
        <p>Add another column</p>
      </button>
      {isOpen && (
        <Modal closeModal={toggleModal}>
          <AddColumnForm />
        </Modal>
      )}
    </>
  );
};

export default AddBtnColumn;
